/*全体*/
.App {
  width: 100%;
  height: 100vh;
  font-size: 18px;
}

img {
  max-width: 100%;
  height: auto;
  vertical-align: bottom;
  border-style: none;
}

.wrapper {
  padding-top: 73px;
}

.section {
  padding: 90px 0;
}

.section:nth-of-type(odd) {
  background-color: #f7f7f7;
}

.container {
  max-width: 1340px;
  margin: 0 auto;
  padding: 0 40px;
}

/*見出し*/
.title {
  font-size: 34px;
  font-weight: bold;
  line-height: 1;
  margin-bottom: 40px;
  text-align: center;
  letter-spacing: 0.05em;
  color: #333;
}

.lead {
  font-size: 16px;
  line-height: 1.8;
  margin-bottom: 30px;
}

/*header*/
header {
  position: fixed;
  top: 0;
  z-index: 1;
  width: 100%;
  background-color: #bab6b6;
  box-shadow: 0 5px 10px -6px rgba(0, 0, 0, 0.1);
}

.header .container {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 20px 40px;
}

.gnav-list {
  display: flex;
  justify-content: space-between;
  list-style: none;
}

.gnav-item:not(:last-child) {
  margin-right: 20px;
}

.gnav-item a {
  position: relative;
  font-size: 13px;
  font-weight: bold;
  display: inline-block;
  padding: 5px 0;
  transition: 0.3s;
  letter-spacing: 0.05em;
  text-decoration: none;
  color: #121111;
}
.gnav-item a:after {
  position: absolute;
  bottom: 0;
  left: 50%;
  width: 0;
  height: 3px;
  content: "";
  transition: 0.3s;
  -webkit-transform: translateX(-50%);
  transform: translateX(-50%);
  background-color: #e70b0b;
}

.gnav-item a:hover:after {
  width: 100%;
}

/*title*/

.mv {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 85vh;
  text-align: center;
  background-color: #d4d3d3;
}

.mv-container {
  padding: 0 40px;
  /* (任意) 中央揃えにすると見栄えが良くなります */
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100px; /* 高さを確保してレイアウト崩れを防ぐ */
}

.mv-title {
  white-space: nowrap;
  font-size: 60px;

  /* === ここを修正しました === */
  /* 1. typing: 2秒かけて文字を表示します */
  /* 2. effect: 2秒間だけカーソルを点滅させます (0.5s * 4回 = 2s) */
  /* 3. hide-cursor: 2秒後にカーソルを完全に非表示にします */
  animation: typing 2s steps(13) forwards, effect 0.5s step-end 4,
    hide-cursor 0s 2s forwards;

  font-weight: bold;
  line-height: 1.2;
  margin-bottom: 5px;
  letter-spacing: 0.1em;
  overflow: hidden;
  border-right: 3px solid;
}

@keyframes typing {
  from {
    width: 0;
  }
  to {
    width: 16ch;
  }
}

/* カーソルが点滅するアニメーション (変更なし) */
@keyframes effect {
  50% {
    border-color: transparent;
  }
}

/* === 新しく追加しました === */
/* カーソルを非表示にするためだけのアニメーション */
@keyframes hide-cursor {
  to {
    border-color: transparent;
  }
}

.mv-subtitle {
  font-size: 20px;
  font-weight: bold;
  line-height: 1;
  margin-bottom: 40px;
  letter-spacing: 0.08em;
}

.mv-text {
  font-size: 16px;
  line-height: 1.8;
}

/*about*/

.profile {
  display: flex;
  flex-wrap: wrap;
  max-width: 800px;
  margin: 0 auto;
}
.profile-img {
  width: 20%;
  margin-right: 30px;
  border-radius: 50%;
}
.profile-img img {
  border-radius: 50%;
}
.profile-body {
  flex: 1;
}

.profile-body p {
  font-size: 15px;
  line-height: 1.8;
}

.profile-body p:not(:last-child) {
  margin-bottom: 30px;
}

/*Work*/
.works-list {
  display: flex;
  flex-wrap: wrap;
  margin-bottom: -40px;
}

.works-item {
  width: 31.74603%;
  margin-right: 1.5873%;
  margin-bottom: 40px;
  color: #333;
}

.works-item:hover {
  opacity: 0.9;
}

.works-item:nth-of-type(3n) {
  margin-right: 0;
}

.works-img img {
  border: 1px solid #e6e6e6;
}

.works-name {
  font-size: 12px;
  font-weight: bold;
  margin-top: 8px;
}

.works-info {
  font-size: 10px;
  margin-top: 5px;
}

/*スキル*/
.title {
  padding: 100px;
}

.skill_section {
  background-color: #d1cfcf;
}

.skill-list {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  margin-bottom: -50px;
}

.skill-item {
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
  width: 48%;
  margin-bottom: 50px;
}

.category {
  text-align: center;
}

.skill-img {
  width: 60px;
  height: auto;
  margin-right: 20px;
}

.skill-body {
  flex: 1;
}

.skill-name {
  margin-bottom: 10px;
}

.skill-text {
  font-size: 14px;
  line-height: 1.8;
}

.tara {
  text-align: center;
}

.collabo {
  text-align: center;
}